ion: absolute;\n    right: 10px;\n    top: 50%;\n    vertical-align: middle;\n  }\n\n  .open & {\n    border-radius: $border-radius-lg $border-radius-lg 0 0;\n    border-bottom-color: $transparent-bg;\n  }\n}\n\n.has-success .input-group-append .input-group-text,\n.has-success .input-group-prepend .input-group-text,\n.has-success .form-control {\n  border-color: lighten($black, 5%);\n}\n\n.has-success .form-control:focus,\n.has-success.input-group-focus .input-group-append .input-group-text,\n.has-success.input-group-focus .input-group-prepend .input-group-text {\n  border-color: darken($success, 10%);\n}\n\n.has-danger .form-control,\n.has-danger .input-group-append .input-group-text,\n.has-danger .input-group-prepend .input-group-text,\n.has-danger.input-group-focus .input-group-prepend .input-group-text,\n.has-danger.input-group-focus .input-group-append .input-group-text {\n  border-color: lighten($danger-states, 5%);\n  color: $danger-states;\n  background-color: rgba(222, 222, 222, 0.1);\n\n  &:focus {\n    background-color: $transparent-bg;\n  }\n}\n\n.has-success,\n.has-danger {\n  &:after {\n    font-family: \"nucleo\";\n    content: \"\\ea1b\";\n    display: inline-block;\n    position: absolute;\n    right: 20px;\n    top: 13px;\n    color: $success;\n    font-size: 11px;\n  }\n\n  &.form-control-lg {\n    &:after {\n      font-size: 13px;\n      top: 24px;\n    }\n  }\n\n  &.has-label {\n    &:after {\n      top: 37px;\n    }\n  }\n\n  &.form-check:after {\n    display: none !important;\n  }\n\n  &.form-check .form-check-label {\n    color: $success;\n  }\n}\n\n.has-danger {\n  &:after {\n    content: \"\\ea48\";\n    color: $danger-states;\n  }\n\n  &.form-check .form-check-label {\n    color: $danger-states;\n  }\n}\n","@mixin input-size($padding-vertical, $padding-horizontal) {\n  padding: $padding-vertical $padding-horizontal;\n}\n\n@mixin form-control-placeholder($color, $opacity) {\n  .form-control::-moz-placeholder {\n    color: $color;\n    @include opacity(1);\n  }\n  .form-control:-moz-placeholder {\n    color: $color;\n    @include opacity(1);\n  }\n  .form-control::-webkit-input-placeholder {\n    color: $color;\n    @include opacity(1);\n  }\n  .form-control:-ms-input-placeholder {\n    color: $color;\n    @include opacity(1);\n  }\n}\n\n@mixin placeholder() {\n  &::-moz-placeholder {\n    @content;\n  } // Firefox\n  &:-ms-input-placeholder {\n    @content;\n  } // Internet Explorer 10+\n  &::-webkit-input-placeholder {\n    @content;\n  } // Safari and Chrome\n}\n\n@mixin light-form() {\n  border-radius: 0;\n  border: 0;\n  padding: 0;\n  background-color: transparent;\n}\n\n@mixin form-control-lg-padding($padding-vertical, $padding-horizontal) {\n  .form-group.no-border.form-control-lg,\n  .input-group.no-border.form-control-lg {\n    .input-group-append .input-group-text {\n      padding: $padding-vertical 0 $padding-vertical $padding-horizontal;\n    }\n\n    .form-control {\n      padding: $padding-vertical $padding-horizontal;\n\n      & + .input-group-prepend .input-group-text,\n      & + .input-group-append .input-group-text {\n        padding: $padding-vertical $padding-horizontal $padding-vertical 0;\n      }\n    }\n  }\n\n  .form-group.form-control-lg,\n  .input-group.form-control-lg {\n    .form-control {\n      padding: $padding-vertical - 1 $padding-horizontal - 1;\n      height: 100%;\n\n      & + .input-group-prepend .input-group-text,\n      & + .input-group-append .input-group-text {\n        padding: $padding-vertical - 1 $padding-horizontal - 1 $padding-vertical -\n          1 0;\n      }\n    }\n\n    .input-group-prepend .input-group-text,\n    .input-group-append .input-group-text {\n      padding: $padding-vertical - 1 0 $padding-vertical $padding-horizontal - 1;\n\n      & + .form-control {\n        padding: $padding-vertical $padding-horizontal - 1 $padding-vertical\n          $padding-horizontal - 3;\n      }\n    }\n  }\n}\n\n@mixin input-base-padding($padding-vertical, $padding-horizontal) {\n  .form-group.no-border,\n  .input-group.no-border {\n    .form-control {\n      padding: $padding-vertical $padding-horizontal;\n\n      & + .input-group-prepend .input-group-text,\n      & + .input-group-append .input-group-text {\n        padding: $padding-vertical $padding-horizontal $padding-vertical 0;\n      }\n    }\n\n    .input-group-prepend .input-group-text,\n    .input-group-append .input-group-text {\n      padding: $padding-vertical 0 $padding-vertical $padding-horizontal;\n    }\n  }\n\n  .form-group,\n  .input-group {\n    .form-control {\n      padding: $padding-vertical - 1 $padding-horizontal - 1 $padding-vertical -\n        1 $padding-horizontal - 1;\n\n      & + .input-group-prepend .input-group-text,\n      & + .input-group-append .input-group-text {\n        padding: $padding-vertical - 1 $padding-horizontal - 1 $padding-vertical -\n          1 0;\n      }\n    }\n\n    .input-group-prepend .input-group-text,\n    .input-group-append .input-group-text {\n      padding: $padding-vertical - 1 0 $padding-vertical - 1 $padding-horizontal -\n        1;\n\n      & + .form-control,\n      & ~ .form-control {\n        padding: $padding-vertical - 1 $padding-horizontal $padding-vertical\n          $padding-horizontal - 3;\n      }\n    }\n  }\n}\n\n//color1 = $opacity-5\n//color2 = $opacity-8\n//color3 = $white-color\n//color4 = $transparent-bg\n//color5 = $opacity-1\n//color6 = $opacity-2\n\n@mixin input-coloured-bg($color1, $color2, $color3, $color4, $color5, $color6) {\n  @include form-control-placeholder(rgba($white, 0.4), 1);\n\n  .form-control {\n    border-color: $color1;\n    color: $color2;\n\n    &:focus {\n      border-color: $color3;\n      background-color: $color4;\n      color: $color3;\n    }\n  }\n\n  .has-success,\n  .has-danger {\n    &:after {\n      color: $color3;\n    }\n  }\n\n  .has-danger {\n    .form-control {\n      background-color: $color4;\n    }\n  }\n\n  .input-group-prepend {\n    margin-right: 0