Dialogs\n// --------------------------------------------------\n.modal-content {\n  border: 0;\n\n  // Modal header\n  // Top section of the modal w/ title and dismiss\n\n  .modal-header {\n    border-bottom: none;\n\n    & button {\n      position: absolute;\n      right: 27px;\n      top: 24px;\n      outline: 0;\n      padding: 1rem;\n      margin: -1rem -1rem -1rem auto;\n    }\n    .title {\n      color: $black;\n      margin-top: 5px;\n      margin-bottom: 0;\n    }\n\n    .modal-title {\n      color: $black;\n    }\n\n    i.tim-icons {\n      font-size: 16px;\n    }\n    .modal-profile {\n      width: 70px;\n      height: 70px;\n      background-color: $white;\n      border-radius: 50%;\n      text-align: center;\n      line-height: 5.7;\n      box-shadow: 0px 5px 50px 0px rgba(0, 0, 0, 0.3);\n\n      i {\n        color: $primary;\n        font-size: 21px;\n        margin-top: -10px;\n      }\n\n      &[class*=\"modal-profile-\"] {\n        i {\n          color: $white;\n        }\n      }\n\n      &.modal-profile-primary {\n        background-color: $primary;\n      }\n\n      &.modal-profile-danger {\n        background-color: $danger;\n      }\n\n      &.modal-profile-warning {\n        background-color: $warning;\n      }\n      &.modal-profile-success {\n        background-color: $success;\n      }\n\n      &.modal-profile-info {\n        background-color: $info;\n      }\n    }\n  }\n\n  // Modal body\n  // Where all modal content resides (sibling of .modal-header and .modal-footer)\n  .modal-body {\n    line-height: 1.9;\n\n    p {\n      color: $black;\n    }\n\n    form .form-check {\n      padding-left: 19px;\n\n      .form-check-label {\n        padding-left: 35px;\n      }\n    }\n\n    img {\n      padding: 10px;\n    }\n  }\n  // Footer (for actions)\n  .modal-footer {\n    border-top: 0;\n    -webkit-justify-content: space-between; /* Safari 6.1+ */\n    justify-content: space-between;\n\n    button {\n      margin: 0;\n      padding-left: 16px